.container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.calculator {
  padding: 20px;
  border: 2px solid black;
  border-radius: 10px;
  background-color: rgb(29, 29, 29);
  width: 360px;
  height: auto;
  max-width: 100%;
  box-sizing: border-box;
}

form .buttons input {
  border: none;
  outline: 0;
  width: 70px;
  height: 70px;
  font-size: 30px;
  font-weight: bold;
  background-color: rgb(71, 71, 71);
  margin: 4px;
  color: white;
  border-radius: 10px;
  cursor: pointer;
}

form .buttons input[type="button"]:hover {
  background-color: rgb(95, 95, 95);
  transition: 0.25s ease;
  box-shadow: 0px 0px 15px 2px rgb(0, 162, 255);
}

form .buttons input[type="button"]:active {
  background-color: rgb(7, 7, 7);
  transition: 0.15s ease;
}

form .display {
  display: flex;
  justify-content: center;
  margin: 5px 0px 15px 0px;
}

form .display input {
  text-align: right;
  width: 100%;
  height: auto;
  flex: 1;
  font-size: 40px;
  padding: 5px 10px;
  color: rgb(230, 230, 230);
  background-color: rgb(88, 88, 88);
  border: 2px solid rgba(184, 184, 184, 0.774);
  border-radius: 10px;
}

form .display input::placeholder {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.521);
}

form input.equal {
  width: 148px;
}

form input.operator {
  background-color: rgb(253, 91, 42);
  font-size: 30px;
}

form input.operator:hover {
  background-color: rgb(253, 122, 82);
}

form input.operator:active {
  background-color: rgb(255, 60, 0);
}

/* Media Query for Mobile Devices */
@media (max-width: 750px) {
  .calculator {
    width: 85%;
    height: auto;
  }

  form .buttons input {
    font-size: 25px;
    width: 70px;
    height: 70px;
    margin: 3.2px;
  }

  form .buttons input[type="button"]:hover {
    box-shadow: none;
  }

  form .buttons input[type="button"]:active {
    box-shadow: 0px 0px 15px 2px rgb(0, 162, 255);
  }

  form .display input {
    font-size: 25px;
    height: 40px;
  }

  form .display input::placeholder {
    font-size: 18px;
  }

  form input.operator {
    font-size: 25px;
  }

  form input.equal {
    width: 145px;
  }
}